/* add css module styles here (optional) */
._1Lxpd {
  position: relative;
  width: 100%;
  scroll-behavior: smooth;
}

._1i4JT {
  overflow: hidden;
  height: 100%;
}

._3AALV {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  padding-right: 20px;
  box-sizing: content-box;

  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}

._3AALV::-webkit-scrollbar {
  display: none;
}

._29gAt {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  pointer-events: none;
}
